import { calculatedPluginSize, CalendarCardPlugin } from '@ali/4ever-bamboo';
import React from 'react';
import zhLocale from '../../../locales/zh_CN';
import { CalendarCardMetadataType, IReceiver } from '../types';

const {
  TotalWarpper,
  TimeTextParagraph,
  PlaceTextParagraph,
  ReceiversParagraph,
  CommentTextParagraph,
  CommentTextWrapper,
  ContainerWrapper,
  BodyWrapper,
  getReceiversHeight,
  CALENDAR_CARD_SINGLE_LINE_HEIGHT,
} = CalendarCardPlugin;

/**
 * 计算应该返回的Receivers数量，标签以中文为准
 * 规则：只取前两行，其中最后一行显示为 等{count}人参会
 * @param receivers 参会人
 * @param organizer 组织者
 * @param containerWidth 容器宽度
 */
export function getCalculatedCalendarReceivers(
  metadata: CalendarCardMetadataType,
  containerWidth: number,
  totalCount: number,
): IReceiver[] {
  const { topReceivers: receivers = [], organizer } = metadata;
  const innerWidth = containerWidth - 52;// 实际容器宽度（border）
  const { locale } = zhLocale.calendarCard;
  const { receiverCount } = locale;

  const totalCountText = receiverCount.replace('{count}', String(totalCount));
  // 最大不超过 1 行
  const maxHeight = (CALENDAR_CARD_SINGLE_LINE_HEIGHT || 1) * 2;
  let index = -1; // 当前Receiver索引
  const len = receivers.length;// 最大两行
  let currHeight = CALENDAR_CARD_SINGLE_LINE_HEIGHT;
  while (currHeight < maxHeight && index < len) {
    index++;
    const currReceivers = receivers.slice(0, index + 1);
    currHeight = getReceiversHeight({
      receivers: currReceivers, locale, totalCountText, singleLineHeight: CALENDAR_CARD_SINGLE_LINE_HEIGHT, innerWidth
    });
  }
  if (index === (len - 1)) {
    if (currHeight <= maxHeight) index += 1;
  }
  return receivers.slice(0, index);
}

export async function calculatedCalendarSize(metadata: CalendarCardMetadataType, containerWidth: number): Promise<number> {
  const { locale } = zhLocale.calendarCard;
  const { time, place, comment } = metadata;

  const containerNode = (
    <div style={{ width: `${containerWidth}px` }}>
      <ContainerWrapper style={{ height: 'auto', width: 'auto' }}>
        <BodyWrapper>
          <TimeTextParagraph>{time}</TimeTextParagraph>
          <PlaceTextParagraph>{place}</PlaceTextParagraph>
          <ReceiversParagraph
            isReadonly
            locale={locale}
            metadata={metadata}
          />
          {comment ? (
            <CommentTextParagraph>
              <CommentTextWrapper>{comment}</CommentTextWrapper>
            </CommentTextParagraph>
          ) : null}
        </BodyWrapper>
      </ContainerWrapper>
    </div>
  );
  const result = await calculatedPluginSize(containerNode, containerWidth);
  return result.height;
}
